body {
  text-align: center;
  opacity: .75;
  background-color: #f0f8ff;
  margin: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  margin-bottom: 20px;
}

img {
  width: 690px;
  max-height: 100%;
}

.breed-select {
  width: 220px;
  height: 50px;
  max-width: 100%;
  text-align: center;
  border: 3px solid #00f;
  border-radius: 5px;
  margin: 10px 0;
  padding: 5px;
}

.loader-container {
  display: none;
}

.loader-container.active {
  height: 100px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

